/* eslint-disable react/jsx-fragments */
import React from 'react';
import { MdClose } from 'react-icons/md';
import './customPrompt.scss';

const Prompt = ({ name, open, toggle, image }) => {
  if (!open) return null;
  return (
    <React.Fragment>
      <div className="prompt-blur" onClick={toggle}>
        <div className="prompt">
          <div className="prompt-1">
            <div className="prompt-head">
              <h3 className="prompt-head-title">{name} <span className="prompt-head-title--span" onClick={!open}><MdClose /></span></h3>
            </div>
            <img src={image} alt={name} className="prompt-img"/>
            <div className="prompt-container">
              <p className="prompt-title-message">Développeur Front-End, Scrum Master</p>
              <p className="prompt-message">En équipe de quatre développeurs, notre projet de fin de formation se résume à reprendre la mécanique des “Livres dont vous êtes le héros” (LDVELH) pour l'adapter à l’univers Web grâce à React, tout en ajoutant une dimension plus ouverte et donc polyvalente à notre version. Les choix laissés à l'utilisateur tout au long de l'histoire lui permettent de vivre une aventure originale et personnalisée.
              </p>
              <hr className="prompt-hr"/>
              <p className="prompt-title-message">HTML, CSS, Javascript, ReactJS, NodeJS, MySQL</p>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Prompt;
