import React, { useState } from 'react';
import './Projets.scss';

import CustomPrompt from './CustomPrompt';


const Projets = () => {

  const [open, isOpen] = useState(false);

    function toggle() {
      isOpen(!open);
    }
  console.log(open);
  return (
    <div className="project" id="project">
      <h2 className="project-title">Projets</h2>
      <div className="project-container">
        <img src="https://i.imgur.com/JEQZuSa.png" alt="gamebook" className="project-container-img" onClick={toggle}/>
        <p className="project-container-title">Gamebook • Site Web</p>
        <p className="project-container-subtitle">Projet de fin de formation</p>
            <CustomPrompt
              open={open}
              name='Gamebook'
              toggle={toggle}
              image="https://i.imgur.com/JEQZuSa.png"
            />
      </div>
    </div>
  )
}

export default Projets;